import styled from 'styled-components'

export const H3 = styled.h3`
    font-weight: 300;
    font-size: 24px;
    text-align: center;
    margin: 10px;
`

export const Label = styled.label`
    font-weight: 300;
    font-size: 16px;
    color: #222;
`



